.about-us-container {
  padding: 3vh 7.5vw;
  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  max-width: 1800px;
  margin: 0 auto;
}

.about-us-wrapper {
  display: flex;
  flex-direction: row;
  background-color: white;
  border-radius: 0.9rem;
  box-shadow: 0 0.375rem 0.75rem rgba(0, 0, 0, 0.1);
  padding: 2.25rem;
  max-width: 75%;
  width: 100%;
  gap: 3rem;
}

.about-us-content {
  display: flex;
  gap: 3rem;
  justify-content: space-between;
  align-items: center;
  flex-wrap: nowrap;
}

.about-us-text {
  flex: 1;
  max-width: 60%;
  padding: 1.5rem;
  color: #333;
}

.about-us-text h1 {
  font-family: "Georgia", serif;
  font-size: clamp(4.5rem, 7.5vw, 6rem);
  color: #841939;
  margin-bottom: 1.5rem;
}

.about-us-text h2 {
  font-family: "Roboto", sans-serif;
  font-size: clamp(2.25rem, 3.75vw, 3rem);
  color: #333;
  margin-bottom: 1.125rem;
}

.about-us-text p {
  font-size: clamp(1.8rem, 3vw, 2.25rem);
  line-height: 1.6;
  margin-bottom: 0.75rem;
}

.about-us-button {
  font-family: "Georgia", serif;
  font-size: clamp(1.5rem, 2.7vw, 1.8rem);
  display: inline-block;
  margin-top: 1.5rem;
  padding: 0.9rem 1.8rem;
  background: #841939;
  color: white;
  font-weight: bold;
  border-radius: 0.45rem;
  text-decoration: none;
  transition: background 0.3s ease;
}

.about-us-button:hover {
  background: #a0243b;
}

.about-us-image {
  flex: 1;
  max-width: 60%;
  text-align: center;
}

.about-us-image img {
  width: 100%;
  max-width: 100%;
  border-radius: 0.45rem;
  box-shadow: 0 0.375rem 0.75rem rgba(0, 0, 0, 0.1);
}

@media (max-width: 768px) {
  .about-us-container {
    flex-direction: column;
    padding: 3vh 7.5vw;
  }

  .about-us-wrapper {
    max-width: 90%;
    padding: 1.5rem;
  }

  .about-us-content {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .about-us-text {
    max-width: 100%;
    padding: 0;
    text-align: center;
  }

  .about-us-image {
    max-width: 100%;
    margin-top: 1.5rem;
  }

  .about-us-text h1 {
    font-size: clamp(3rem, 7vw, 4.5rem);
  }

  .about-us-text h2 {
    font-size: clamp(1.8rem, 4vw, 2.5rem);
  }

  .about-us-text p {
    font-size: clamp(1.5rem, 3vw, 1.8rem);
  }

  .about-us-button {
    font-size: clamp(1.2rem, 2.5vw, 1.5rem);
    padding: 0.75rem 1.5rem;
  }
}