.logo-img {
  height: 15vh;
  width: auto;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2vh 3vw;
  box-shadow: 0 1rem 1rem black;
  background-size: cover;
  background-position: center;
}

.logo-container {
  padding: 1rem;
  border-radius: 0.6rem;
  background-color: #fff;
}

.nav-container {
  padding: 1rem;
  border-radius: 0.6rem;
  background-color: transparent;
}

.nav {
  display: flex;
  padding-right: 10vw;
  gap: 5vw;
}

.nav-item {
  font-family: "Georgia";
  font-weight: 400;
  position: relative;
  font-size: 3rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 1.5rem;
  color: #841939;
}

.nav-item:hover {
  color: #841939;
}

.agenda-container {
  padding: 1rem;
  border-radius: 0.6rem;
  background-color: transparent;
}

.header-right {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.header-link {
  font-family: "Georgia";
  font-weight: 400;
  font-size: 2rem;
  color: white;
  text-decoration: none;
}

.header-link:hover {
  color: #3498db;
}

@media (max-width: 768px) {
  .header {
    flex-direction: column;
    align-items: flex-start;
    padding: 2vh 2vw;
  }

  .nav {
    flex-direction: column;
    padding-right: 0;
    gap: 2rem;
  }

  .nav-item {
    font-size: 2rem;
    gap: 1rem;
  }

  .header-right {
    gap: 1.5rem;
  }

  .header-link {
    font-size: 1.8rem;
  }
}