.services-container {
  padding: 5vh;
  text-align: center;
  background-color: transparent;
}

.services-title {
  font-family: "Georgia";
  font-size: 2.5rem;
  margin-bottom: 2rem;
  color: white;
  text-align: left;
  display: flex;
  align-items: center;
  gap: 1rem;
  background: #941b50;
  padding: 1rem 2rem;
  border-radius: 0.6rem;
  box-shadow: 0 0.5rem 1rem maroon;
}

.services-list {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.service-card {
  background-color: transparent;
  border-radius: 0.6rem;
  overflow: hidden;
  box-shadow: 0 0.5rem 1rem maroon;
  display: flex;
  flex-direction: column;
  text-align: left;
  transition: transform 0.3s ease;
  align-items: stretch;
}

.service-card:hover {
  transform: translateY(-0.8rem);
}

.service-content-container {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  padding: 2rem;
}

.service-icon {
  width: 10vw;
  display: flex;
  justify-content: center;
  align-items: center;
  color: floralwhite;
}

.service-content {
  flex: 1;
}

.service-title {
  font-size: 2rem;
  margin: 0;
  color: whitesmoke;
}

.service-description {
  font-size: 1.5rem;
  margin-top: 0.8rem;
  color: whitesmoke;
}

.service-dropdown-toggle {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0.8rem;
}

.service-dropdown {
  padding: 2rem;
  background-color: white;
  border-top: 0.1rem solid #ddd;
}

.consultation-info {
  font-size: 2rem;
}

.appointment-button {
  font-family: "Georgia";
  display: inline-block;
  margin-top: 2rem;
  padding: 1rem 2rem;
  background-color: #941b50;
  color: white;
  font-size: 1.5rem;
  text-decoration: none;
  border-radius: 0.6rem;
  transition: background-color 0.3s ease;
}

.appointment-button:hover {
  background-color: #800000;
}

.doctors-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 2rem;
}

.doctor-card {
  background-color: #f9f9f9;
  padding: 2rem;
  border-radius: 0.6rem;
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.1);
}

.doctor-card p,
.doctor-card ul {
  font-size: 1.4rem;
  color: #333;
}

.doctor-services-list {
  list-style-type: disc;
  margin-left: 2rem;
}

@media (max-width: 768px) {
  .service-content-container {
    flex-direction: column;
    align-items: center;
  }

  .service-icon {
    width: 100%;
  }

  .service-content {
    align-items: center;
    text-align: center;
  }

  .services-title {
    font-size: 2rem;
  }

  .doctors-grid {
    grid-template-columns: 1fr;
  }

  .appointment-button {
    font-size: 1.2rem;
    padding: 0.8rem 1.5rem;
  }
}