@import "~slick-carousel/slick/slick.css";
@import "~slick-carousel/slick/slick-theme.css";

.preguntas-container {
  display: flex;
  justify-content: space-between;
  padding: 5vh 2vw;
}

.faq-container,
.testimonials-container {
  flex: 1;
  width: 50%;
  max-width: 50%;
  overflow: hidden;
}

.section-title {
  font-size: 2.5rem;
  font-family: "Georgia";
  background-color: #941b50;
  color: white;
  padding: 0.8rem 1.5rem;
  border-radius: 0.6rem;
  display: inline-block;
}

.faq-item {
  margin-bottom: 1.5rem;
  border: 0.1rem solid #ddd;
  border-radius: 0.6rem;
  padding: 1rem;
  background-color: #fff;
  box-shadow: 0 0.5rem 1rem maroon;
  max-width: 90%;
}

.faq-question {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
  font-size: 3rem;
  color: #333;
}

.faq-answer {
  padding: 1rem;
  border-top: 0.1rem solid #ddd;
  color: white;
  background-color: #941b50;
  font-size: 1.8rem;
}

.testimonials-container {
  position: relative;
  max-width: 90vw;
  margin: 0 auto;
  padding: 2rem;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  text-align: center;
  overflow: hidden;
}

.testimonial-item {
  margin: 0 auto 2rem auto;
  border: 0.1rem solid #ddd;
  border-radius: 0.6rem;
  padding: 2rem;
  background-color: #fff;
  box-shadow: 0 0.5rem 1rem maroon;
  font-size: 1.8rem;
  max-width: 70rem;
  width: 100%;
  text-align: center;
  box-sizing: border-box;
  height: auto;
  word-wrap: break-word;
}

@media (max-width: 768px) {
  .testimonials-container {
    max-width: 100%;
    padding: 1.5rem;
  }

  .preguntas-container {
    flex-direction: column;
    align-items: center;
    padding: 3vh 2vw;
  }

  .faq-container,
  .testimonials-container {
    width: 100%;
    max-width: 100%;
  }

  .faq-item {
    max-width: 100%;
    margin-bottom: 1.5rem;
  }

  .section-title {
    font-size: 2rem;
  }

  .faq-question {
    font-size: 2rem;
  }

  .faq-answer {
    font-size: 1.4rem;
  }

  .testimonial-item {
    max-width: 100%;
    font-size: 1.6rem;
    margin-bottom: 1.5rem;
    height: auto;
  }
}