.contact-info {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3vh 2vw;
}

.contact-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 2rem;
  max-width: 800px;
  padding: 2rem;
  border: 0.2rem solid #841939;
  border-radius: 0.8rem;
  box-shadow: 0 0.5rem 1rem maroon;
  background: white;
  text-align: center;
}

.contact-text {
  font-size: 1.8rem;
  color: #333;
}

.contact-text h2 {
  font-family: "Georgia";
  font-size: 2.4rem;
  color: #841939;
  margin-bottom: 1rem;
}

.contact-text p {
  margin: 0.5rem 0;
}

.map-container {
  width: 100%;
  max-width: 600px;
  height: 400px;
  border-radius: 0.8rem;
  overflow: hidden;
}